import React, { useState } from 'react'
import Modal from 'antd/es/modal'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import Button from 'antd/es/button'
import Input from 'antd/es/input'
import InputNumber from 'antd/es/input-number'
import Form from 'antd/es/form'
import Select from 'antd/es/select'

import Upload from 'antd/es/upload'
import Divider from 'antd/es/divider'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import { uploadFile } from '../../utils/uploadFile'
import {
    descriptionRules,
    nameRules,
    priceRules,
} from '../../constants/fieldRules'
import connectionsSelect from '../EditGoalAndProgressForm/connectionsSelect'
import Descriptions from 'antd/es/descriptions'
import UserButton from '../UserButton/UserButton'
import Dropdown from 'antd/es/dropdown'
import Menu from 'antd/es/menu'
import Card from 'antd/es/card'
import message from 'antd/es/message'
import MoreIcon from '../UI/MoreIcon'
import ImageCarousel from '../UI/ImageCarousel/ImageCarousel'
import Tag from 'antd/es/tag'

const Rewards = props => {
    const [previewImage, setPreviewImage] = useState('')
    const [previewReward, setPreviewReward] = useState()
    const [editedReward, setEditedReward] = useState()
    const [editedRewardIndex, setEditedRewardIndex] = useState()
    const { account, friendData, readOnly, progress } = props

    const RewardButton = props => (
        <span
            style={{ fontWeight: 'bold', cursor: 'pointer' }}
            onClick={() => setPreviewReward(props.item)}
        >
            {props.item.mode === 'simple'
                ? props.item.simple
                : props.item.mode === 'money'
                ? props.item.money + ' (money)'
                : props.item.mode === 'item'
                ? props.item.itemName
                : ''}
        </span>
    )
    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }

    return props.rewards ? (
        <div>
            {props.onlyButton ? (
                props.rewards.map((item, index) => (
                    <RewardButton item={item} key={index} />
                ))
            ) : (
                <Form {...layout} style={{ margin: 0 }}>
                    <Form.Item
                        label={props.label}
                        key={props.rewardKey}
                        style={{ margin: 0 }}
                    >
                        {props.rewards.length > 0 && (
                            <ul
                                style={{
                                    paddingLeft: '5px',
                                    listStyleType: 'none',
                                }}
                            >
                                {props.rewards.map((item, index) => {
                                    return (
                                        <li key={index}>
                                            <Tag>
                                                <div
                                                    style={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                    }}
                                                >
                                                    <div>
                                                        <RewardButton
                                                            item={item}
                                                        />
                                                        {' from '}
                                                        <UserButton
                                                            id={item.owner}
                                                            noAva={true}
                                                            inline={true}
                                                        />
                                                        {' for '}
                                                        {item.for &&
                                                        item.for.length > 0 ? (
                                                            <UserButton
                                                                id={item.for}
                                                                noAva={true}
                                                                inline={true}
                                                            />
                                                        ) : (
                                                            <span
                                                                style={{
                                                                    fontWeight:
                                                                        'bold',
                                                                    marginLeft:
                                                                        '5px',
                                                                }}
                                                            >
                                                                All
                                                            </span>
                                                        )}
                                                    </div>
                                                    {item.owner ===
                                                        account._id &&
                                                        !readOnly && (
                                                            <>
                                                                <Dropdown
                                                                    overlay={
                                                                        <Menu>
                                                                            <Menu.Item
                                                                                onClick={() => {
                                                                                    setEditedRewardIndex(
                                                                                        index
                                                                                    )
                                                                                    setEditedReward(
                                                                                        item
                                                                                    )
                                                                                }}
                                                                            >
                                                                                Edit
                                                                            </Menu.Item>

                                                                            <Menu.Item
                                                                                onClick={() => {
                                                                                    if (
                                                                                        !window.confirm(
                                                                                            'Are you sure you want to delete this item?'
                                                                                        )
                                                                                    )
                                                                                        return
                                                                                    if (
                                                                                        props.setRewards
                                                                                    ) {
                                                                                        props.setRewards(
                                                                                            props.rewards.filter(
                                                                                                (
                                                                                                    item,
                                                                                                    indexIn
                                                                                                ) =>
                                                                                                    indexIn !==
                                                                                                    index
                                                                                            )
                                                                                        )
                                                                                    } else {
                                                                                        wsActions.deleteReward(
                                                                                            account._id,
                                                                                            progress._id,
                                                                                            item.rewardId
                                                                                        )
                                                                                    }
                                                                                }}
                                                                            >
                                                                                Delete
                                                                            </Menu.Item>
                                                                        </Menu>
                                                                    }
                                                                >
                                                                    <Button type="link">
                                                                        <MoreIcon />
                                                                    </Button>
                                                                </Dropdown>
                                                            </>
                                                        )}
                                                </div>
                                            </Tag>
                                        </li>
                                    )
                                })}
                            </ul>
                        )}

                        {!readOnly && (
                            <Button
                                type="link"
                                style={{ paddingLeft: 0, color: '#1890ff' }}
                                onClick={() => {
                                    if (account && account._id) {
                                        setEditedRewardIndex('new')
                                        setEditedReward({ mode: 'item' })
                                    } else {
                                        message.warning(
                                            'You will be able to add rewards after you sign up.'
                                        )
                                    }
                                }}
                            >
                                <PlusOutlined />
                                Add rewards
                            </Button>
                        )}
                    </Form.Item>
                </Form>
            )}
            <Modal
                visible={editedReward}
                title={
                    editedReward === 'new' ? 'Add new reward' : 'Edit reward'
                }
                closable={true}
                destroyOnClose={true}
                onCancel={() => {
                    setEditedReward()
                    document.body.removeAttribute('style')
                }}
                onOk={() => {
                    let rewardToSave = {}
                    if (editedReward.mode === 'money') {
                        rewardToSave.money = editedReward.money
                        rewardToSave.mode = editedReward.mode
                        rewardToSave.for = editedReward.for
                        rewardToSave.rewardId = editedReward.rewardId
                    } else {
                        rewardToSave = { ...editedReward, money: undefined }
                    }
                    rewardToSave.owner = account._id

                    if (props.setRewards) {
                        let newRewards = [...props.rewards]

                        if (editedRewardIndex === 'new')
                            newRewards = [...newRewards, rewardToSave]
                        else
                            newRewards = newRewards.map((item, index) => {
                                if (index === editedRewardIndex)
                                    return rewardToSave
                                else return item
                            })
                        props.setRewards(newRewards)
                        setEditedReward()
                        setEditedRewardIndex()
                    } else {
                        wsActions.saveReward(
                            account._id,
                            progress._id,
                            props.rewardKey,
                            rewardToSave
                        )
                        setEditedReward()
                        setEditedRewardIndex()
                    }
                    document.body.removeAttribute('style')
                }}
                destroyOnClose={true}
                okText="Save"
            >
                {editedReward && (
                    <>
                        <Form {...layout}>
                            <Form.Item label="Reward for">
                                {connectionsSelect(
                                    account,
                                    friendData,
                                    editedReward.for,
                                    value => {
                                        setEditedReward({
                                            ...editedReward,
                                            for: value,
                                        })
                                    }
                                )}
                                <p>(leave blank for all)</p>
                            </Form.Item>
                            <Divider type="horizontal" />

                            <Form.Item label="Images">
                                <Upload
                                    listType="picture-card"
                                    fileList={(
                                        editedReward.itemImages || []
                                    ).map((url, index) => ({
                                        url,
                                        uid: index,
                                    }))}
                                    onPreview={file => {
                                        setPreviewImage(file.url)
                                    }}
                                    onChange={({ fileList }) => {
                                        setEditedReward({
                                            ...editedReward,
                                            itemImages: fileList.map(
                                                image => image.url
                                            ),
                                        })
                                    }}
                                    customRequest={({ file }) => {
                                        uploadFile(
                                            file,
                                            800,
                                            undefined,
                                            url => {
                                                setEditedReward({
                                                    ...editedReward,
                                                    itemImages: [
                                                        ...(editedReward.itemImages ||
                                                            []),
                                                        url,
                                                    ],
                                                })
                                            }
                                        )
                                    }}
                                >
                                    {(editedReward.itemImages || []).length >=
                                    8 ? null : (
                                        <div>
                                            <PlusOutlined />
                                            <div className="ant-upload-text">
                                                Upload
                                            </div>
                                        </div>
                                    )}
                                </Upload>
                            </Form.Item>
                            <Form.Item label="Name" rules={nameRules}>
                                <Input
                                    value={editedReward.itemName}
                                    onChange={({ target: { value } }) =>
                                        setEditedReward({
                                            ...editedReward,
                                            itemName: value,
                                        })
                                    }
                                />
                            </Form.Item>
                            <Form.Item
                                label="Description"
                                rules={descriptionRules}
                            >
                                <Input.TextArea
                                    value={editedReward.itemDescription}
                                    onChange={({ target: { value } }) =>
                                        setEditedReward({
                                            ...editedReward,
                                            itemDescription: value,
                                        })
                                    }
                                />
                            </Form.Item>
                        </Form>
                    </>
                )}
            </Modal>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
                destroyOnClose={true}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
            <Modal
                visible={previewReward}
                footer={null}
                onCancel={() => {
                    setPreviewReward()
                    document.body.removeAttribute('style')
                }}
                title="Reward details"
                destroyOnClose={true}
            >
                {previewReward ? (
                    <>
                        <ImageCarousel images={previewReward.itemImages} />

                        {previewReward.mode === 'item' && (
                            <h2>{previewReward.itemName}</h2>
                        )}
                        {previewReward.mode === 'item' && (
                            <p style={{ whiteSpace: 'pre-wrap' }}>
                                {previewReward.itemDescription}
                            </p>
                        )}
                        {previewReward.mode === 'simple' && (
                            <p style={{ whiteSpace: 'pre-wrap' }}>
                                {previewReward.simple}
                            </p>
                        )}
                    </>
                ) : null}
            </Modal>
        </div>
    ) : null
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        friendData: state.friendData,
        progress: state.progress,
    }
}

export default withRouter(connect(mapStateToProps)(Rewards))
