import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import Post from '../../components/Post/Post'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'
import UserButton from '../../components/UserButton/UserButton'
import Helmet from 'react-helmet'
import Button from 'antd/es/button'
import Modal from 'antd/es/modal/Modal'
import AddPerk from '../Shop/AddPerk'
import Popconfirm from 'antd/es/popconfirm'
import * as wsActions from '../../websocketActions'
const WishlistItem = props => {
    const [data, setData] = useState({})
    const [editedPerk, setEditedPerk] = useState('')
    const [editPerkOpen, setEditPerkOpen] = useState()
    const { account } = props

    let accountId
    const pathParts = props.history.location.pathname.split('/')
    if (pathParts.length > 3) {
        accountId = pathParts[2]
    } else return null

    useEffect(() => {
        if (!props.isAuthed || account._id === accountId) {
            pullData(props, data, setData, { forceAuth: false }, data => {
                const friendData = {}
                for (let friend of data.friendData)
                    friendData[friend._id] = friend

                props.setData({
                    post: { [data.post._id]: data.post },
                    friendData,
                })
            })
        }
    }, [props.isAuthed])

    let wishlistItem, profile
    if (props.isAuthed && account._id === accountId) {
        wishlistItem = account.wishlist.find(
            item => item.itemId === pathParts[3]
        )

        profile = account
    } else {
        wishlistItem = data.wishlistItem
        profile = data.profile
    }
    if (!wishlistItem) return null
    props.setData({ currentPosts: wishlistItem.post })

    const isOwner =
        account &&
        account._id &&
        profile &&
        profile._id &&
        account._id === profile._id

    return wishlistItem ? (
        <div className="contentDiv">
            <Helmet>
                <title>{`${wishlistItem.name} by ${profile.name}`}</title>
                <meta name="description" content={wishlistItem.description} />
            </Helmet>
            <Card
                title={
                    <span>
                        <UserButton id={profile._id} /> wishlist
                    </span>
                }
                bordered={false}
            >
                <ImageCarousel images={wishlistItem.images} />
                <div style={{ textAlign: 'center' }}>
                    <h1>{wishlistItem.name}</h1>
                    {isOwner && (
                        <>
                            <Button
                                type="primary"
                                style={{ margin: '10px' }}
                                onClick={() => {
                                    setEditedPerk(wishlistItem.itemId)
                                    setEditPerkOpen(true)
                                }}
                            >
                                Edit
                            </Button>

                            <Popconfirm
                                title="Are you sure you want to delete this item?"
                                onConfirm={() => {
                                    wsActions.deletePerk(
                                        account._id,
                                        wishlistItem.itemId,
                                        'wishlist'
                                    )
                                    props.history.push(
                                        '/profile/' + profile._id
                                    )
                                }}
                                onCancel={() => {}}
                                okText="Confirm"
                                cancelText="Cancel"
                            >
                                <Button
                                    danger
                                    type="text"
                                    style={{ margin: '10px' }}
                                >
                                    Delete
                                </Button>
                            </Popconfirm>
                        </>
                    )}
                </div>
                <p style={{ whiteSpace: 'pre-wrap' }}>
                    {wishlistItem.description}
                </p>
                <Post
                    noStartMessage={true}
                    post={props.post[wishlistItem.post]}
                    account={props.account}
                    friendData={props.friendData}
                    showReplies={true}
                />
            </Card>
            <Modal
                title="Edit item"
                visible={editPerkOpen}
                maskClosable
                closable
                onCancel={() => {
                    setEditPerkOpen(false)
                    document.body.removeAttribute('style')
                }}
                width="90%"
                footer={null}
                destroyOnClose={true}
            >
                <AddPerk
                    setEditPerkOpen={setEditPerkOpen}
                    perkId={editedPerk}
                    mode="wishlist"
                />
            </Modal>
        </div>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        post: state.post,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(WishlistItem)
)
