import React, { useState } from 'react'
import Modal from 'antd/es/modal'
import AddPerk from './AddPerk'
import PerkCard from './PerkCard'
import AddCard from '../../components/AddCard/AddCard'
import MasonryGrid from '../../components/MasonryGrid'

const WishlistAndPerks = props => {
    const [editedPerk, setEditedPerk] = useState('')
    const [editPerkOpen, setEditPerkOpen] = useState()

    const { account, profile, isOwner } = props

    const editPerk = (
        <Modal
            title="Add item"
            visible={editPerkOpen}
            maskClosable
            closable
            onCancel={() => {
                setEditPerkOpen(false)
                document.body.removeAttribute('style')
            }}
            width="90%"
            footer={null}
            destroyOnClose={true}
        >
            <AddPerk
                setEditPerkOpen={setEditPerkOpen}
                perkId={editedPerk}
                mode={props.mode}
            />
        </Modal>
    )

    return account && account._id && isOwner ? (
        <>
            {isOwner && (
                <>
                    <AddCard
                        action={() => {
                            setEditedPerk('')
                            setEditPerkOpen(true)
                        }}
                        title="Add item"
                        sub={props.mode === 'shop' && 'Set price'}
                    />
                    {editPerk}
                </>
            )}
            <MasonryGrid>
                {account[props.listKey || props.mode] &&
                    account[props.listKey || props.mode].map((perk, index) => (
                        <PerkCard
                            key={index}
                            perk={perk}
                            perkId={
                                props.mode === 'wishlist' ||
                                props.mode === 'goals'
                                    ? perk.itemId
                                    : perk.perkId
                            }
                            owner={profile}
                            account={account}
                            setEditedPerk={setEditedPerk}
                            setEditPerkOpen={setEditPerkOpen}
                            mode={props.mode}
                            style={{ cursor: 'pointer' }}
                        />
                    ))}
            </MasonryGrid>
        </>
    ) : (
        <MasonryGrid>
            {profile.wishlist.map((item, index) => (
                <PerkCard
                    key={index}
                    perk={item}
                    perkId={item.itemId}
                    owner={profile}
                    account={account || {}}
                    setEditedPerk={() => {}}
                    setEditPerkOpen={() => {}}
                    onlyBuy={true}
                    mode="wishlist"
                />
            ))}
        </MasonryGrid>
    )
}

export default WishlistAndPerks
