import React from 'react'
import { connect } from 'react-redux'
import * as actions from './store/actions'

const RNBridge = props => {
    window.addEventListener('message', function(e) {
        // window.isRNWebView = true
        console.log('message received from react native')

        try {
            const id = e.data
            console.log(id)
            props.setIsAuthed(id)
        } catch (err) {
            console.error('failed to parse message from react-native ' + err)
            return
        }
    })

    // window.counter = 0
    // function clickHandler() {
    //     window.counter++
    //     window.webViewBridge.send(
    //         'handleDataReceived',
    //         window.counter,
    //         function(res) {
    //             window.document
    //                 .getElementById('button')
    //                 .setAttribute('style', 'background-color: ' + res)
    //         },
    //         function(err) {
    //             window.document
    //                 .getElementById('container')
    //                 .setAttribute('style', 'background-color: ' + err)
    //         }
    //     )
    // }

    return <></>
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setIsAuthed: id => dispatch(actions.setIsAuthed(id)),
    }
}

export default connect(
    null,
    mapDispatchToProps
)(RNBridge)
