import React from 'react'
import { Link, withRouter } from 'react-router-dom'

export default withRouter(props => {
    const placeholderImages = [
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/image+addspire-02.jpg',
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/pexels-adrianna-calvo-3719018.jpg',
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/pexels-george-stremplis-1006190.jpg',
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/pexels-pixabay-33129.jpg',
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/pexels-polina-tankilevitch-3905859.jpg',
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/pexels-quang-nguyen-vinh-3355732.jpg',
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/pexels-quang-nguyen-vinh-3355788.jpg',
        'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/pexels-sittisak-c-1739347.jpg',
    ]
    const images = props.images || []

    const getPH = () => {
        if (!props.id) return 0
        const makeRand = x => {
            x = Math.abs(x)
            while (x > 1) x = x / 10
            return x
        }
        const matches = props.id.match(/\d+$/)
        const imagePH = matches && matches.length > 0 ? parseInt(matches[0]) : 0
        return Math.abs(
            Math.round(makeRand(imagePH) * (placeholderImages.length - 1))
        )
    }

    const Card = (
        <>
            {!props.friends && (
                <img
                    style={{
                        maxWidth: '100%',
                        margin: 'auto',
                        display: 'block',
                        borderRadius: '15px',
                        ...props.style,
                    }}
                    src={
                        images.length > 0
                            ? images[0]
                            : placeholderImages[getPH()]
                    }
                />
            )}

            <div style={{ padding: '5px 0px', wordBreak: 'break-word' }}>
                {props.actions && (
                    <div style={{ float: 'right' }}>{props.actions}</div>
                )}
                <span style={{ float: 'none' }}>{props.body}</span>
            </div>
        </>
    )
    return props.noLink ? (
        Card
    ) : (
        <Link to={props.onClickLink} style={{ cursor: 'pointer' }}>
            {Card}
        </Link>
    )
})
